
.login-password .el-input {
  margin-top: 10px;
}
.login-password .el-select {
  width: 318px;
}
.login-password .verify {
  position: relative;
}
.login-password .verify .el-input .el-input__inner {
    padding-right: 145px;
}
.login-password .verify .code-img {
    position: absolute;
    right: 0;
    top: 15px;
    overflow: hidden;
}
.login-password .verify .code-img img {
      float: left;
      height: 30px;
}
.login-password .verify .code-img i {
      width: 30px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      float: right;
      font-size: 18px;
      cursor: pointer;
      margin: 0 5px;
}
.login-password .verify .code-img i:before {
        width: 20px;
        height: 20px;
}
.login-password .forget {
  margin-top: 30px;
  color: #E6A23B;
  padding-right: 2px;
  text-align: right;
}
.login-password .forget a {
    color: #E6A23B;
}
.login-password .el-button {
  width: 100%;
  margin-top: 30px;
}



