
.withdrawal__container {
  width: 1250px;
  margin: 0 auto;
  top: 20px;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__stepbar {
    margin: 20px 20px 0 20px;
    text-align: center;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__stepbar .el-col {
      color: #E0E0E0;
      padding-bottom: 20px;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__stepbar .el-col.active {
        color: #333;
        border-bottom: 1px solid #333;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper {
    margin: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .el-col:first-child {
      border-right: 1px solid #ddd;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .withdrawal__form {
      padding: 50px 0;
      margin: 0 auto;
      width: 310px;
      min-height: 320px;
      display: table;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .withdrawal__form .el-form {
        display: table-cell;
        vertical-align: middle;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .withdrawal__form .tips__text {
        font-size: 12px;
        color: #999999;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .withdrawal__form .success {
        font-size: 16px;
        color: #333;
        display: table-cell;
        vertical-align: middle;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .withdrawal__form .success div.u-align-c:first-child {
          margin-bottom: 40px;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .withdrawal__form .success .circle {
          width: 40px;
          height: 40px;
          background: #f5f5f5;
          border-radius: 100%;
          display: inline-block;
          border: 1px solid #ddd;
          text-align: center;
          line-height: 40px;
          border-radius: 100%;
          display: inline-block;
          margin-right: 20px;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .withdrawal__form .success .circle i {
            color: #ddd;
            font-size: 20px;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .notic__wrapper {
      min-height: 320px;
      display: table;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .notic__wrapper .notic {
        padding: 50px 0;
        text-align: center;
        display: table-cell;
        vertical-align: middle;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .notic__wrapper .notic img {
          width: 130px;
}
.withdrawal__container .withdrawal__wrapper .withdrawal__form__wrapper .notic__wrapper .notic p {
          width: 280px;
          margin: 0 auto;
          margin-top: 30px;
          color: #999;
}



