
.my__order__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
}
.my__order__container .order__wrapper .list__container {
    padding: 0 20px;
}
.my__order__container .order__wrapper .list__container .list__header {
      height: 50px;
      line-height: 50px;
      text-align: center;
      background: #fafafa;
}
.my__order__container .order__wrapper .list__container .comment-list .el-row {
      line-height: 50px;
      height: 50px;
}
.my__order__container .order__wrapper .list__container .comment-list .el-row .el-col .el-rate {
        line-height: 50px;
        font-size: 16px;
}



