
.recharge__container {
  width: 1250px;
  margin: 0 auto;
  top: 20px;
}
.recharge__container .recharge__wrapper .recharge__form__wrapper {
    margin: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.recharge__container .recharge__wrapper .recharge__form__wrapper .el-col:first-child {
      border-right: 1px solid #ddd;
}
.recharge__container .recharge__wrapper .recharge__form__wrapper .recharge__form {
      padding: 50px 0;
      margin: 0 auto;
      width: 310px;
}
.recharge__container .recharge__wrapper .recharge__form__wrapper .notic {
      padding: 50px 0;
      text-align: center;
}
.recharge__container .recharge__wrapper .recharge__form__wrapper .notic img {
        width: 130px;
}
.recharge__container .recharge__wrapper .recharge__form__wrapper .notic p {
        width: 280px;
        margin: 0 auto;
        margin-top: 30px;
        color: #999;
}



