
.my__address__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
}
.my__address__container .address__list {
    padding: 10px;
}
.my__address__container .address__list .el-col {
      padding: 10px;
}
.my__address__container .address__list .el-col .address__list__wrapper {
        border: 1px solid #ddd;
        padding: 15px 20px;
        border-radius: 4px;
        min-height: 132px;
}
.my__address__container .address__list .el-col .address__list__wrapper .add__address {
          text-align: center;
}
.my__address__container .address__list .el-col .address__list__wrapper .add__address i {
            font-size: 20px;
            color: #ccc;
            position: relative;
            top: 2px;
}
.my__address__container .address__list .el-col .address__list__wrapper .add__address span {
            margin-left: 20px;
}



