
@charset "UTF-8";
.refund__detail__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
  background: #fff;
}
.refund__detail__container .refund__detail__wrapper .info__list {
    border: 1px solid #ddd;
    margin-right: 40px;
    min-height: 650px;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item {
      margin-bottom: 60px;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item .el-form-item {
        margin-bottom: 0;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item .title {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid #ddd;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item .title h2 {
          font-size: 16px;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item img {
        width: 50px;
        height: 50px;
        float: left;
        margin-right: 20px;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item .shop__name {
        font-size: 16px;
        color: #333;
        height: 50px;
        overflow: hidden;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item .shop__tel {
        color: #999;
        height: 50px;
        padding-left: 20px;
        word-break: break-all;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item .refund__money {
        font-size: 20px;
        color: #FF644C;
}
.refund__detail__container .refund__detail__wrapper .info__list .list__item .todo--btn {
        position: absolute;
        top: 5px;
        right: 0;
}
.refund__detail__container .refund__detail__wrapper .info__step {
    margin-left: 40px;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container {
      border-left: 1px solid #ddd;
      height: 239px;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__wrapper {
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.green .icon__wrapper {
        border: 1px solid #67C33A;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.green .icon__wrapper .iconfont {
          color: #67C33A;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.green .step__con .status span {
        color: #67C33A;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.black .icon__wrapper {
        border: 1px solid #333;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.black .icon__wrapper .iconfont {
          color: #333;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.black .step__con .status span {
        color: #333;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.gray .icon__wrapper {
        border: 1px solid #ddd;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.gray .icon__wrapper .iconfont {
          color: #ddd;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.gray .step__con {
        background: #f5f5f5;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.gray .step__con .status span {
          color: #ddd;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.gray .step__con .status p {
          color: #ddd;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container.gray .step__con .arrow span {
          border-color: #f5f5f5 transparent transparent;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .icon__wrapper {
        position: absolute;
        left: -16px;
        background: #fff;
        border-radius: 100%;
        width: 32px;
        height: 32px;
        text-align: center;
        line-height: 32px;
        top: 29px;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con {
        border: 1px solid #ddd;
        border-radius: 4px;
        background: #fff;
        height: 90px;
        padding: 0 30px;
        margin-left: 40px;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con .arrow {
          position: absolute;
          bottom: 52px;
          left: 0;
          transform: rotate(90deg);
          -ms-transform: rotate(90deg);
          /* IE 9 */
          -moz-transform: rotate(90deg);
          /* Firefox */
          -webkit-transform: rotate(90deg);
          /* Safari 和 Chrome */
          -o-transform: rotate(90deg);
          /* Opera */
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con .arrow em {
            border-color: #ddd transparent transparent;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con .arrow span {
            border-color: #FFF transparent transparent;
            top: -1px;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con .arrow * {
          display: block;
          border-width: 8px;
          position: absolute;
          border-style: solid dashed dashed dashed;
          font-size: 0;
          line-height: 0;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con .status span {
          display: block;
          margin-bottom: 10px;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con .status p {
          position: relative;
          font-size: 12px;
          color: #666;
          margin-bottom: 10px;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con .time {
          float: right;
}
.refund__detail__container .refund__detail__wrapper .info__step .step__container .step__con .time span {
            font-size: 12px;
}



