
.fund-dialog .el-dialog__title {
  color: #333;
  font-size: 16px;
}
.fund-dialog .el-dialog__body {
  padding: 30px;
}
.fund-dialog .el-dialog__header {
  background: #F5F5F5;
}
.fund-dialog .code {
  overflow: hidden;
}
.fund-dialog .code img {
    float: left;
    height: 30px;
    margin-top: 5px;
    width: 100px;
    cursor: pointer;
}
.fund-dialog .code p.loading {
    float: left;
    width: 100px;
    height: 40px;
    line-height: 40px;
    padding: 0;
    text-align: left;
}
.fund-dialog .code .el-input {
    float: left;
    width: 180px;
    margin-left: 30px;
}
.fund-dialog .code .el-button {
    float: right;
    width: 130px;
}
.fund-dialog .form-group {
  overflow: hidden;
  margin-top: 20px;
}
.fund-dialog .form-group:first-child {
    margin-top: 0;
}
.fund-dialog .form-group label {
    float: left;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    width: 100px;
    text-align: right;
}
.fund-dialog .form-group .el-input {
    float: left;
    width: 410px;
    margin-left: 30px;
}
.fund-dialog .form-group p.phone {
    height: 40px;
    line-height: 40px;
    margin-left: 30px;
    float: left;
    color: #666;
}
.fund-dialog .form-group.special .el-input {
    float: left;
    width: 270px;
    margin-left: 30px;
}
.fund-dialog .form-group.special .el-button {
    margin-left: 20px;
}
.fund-dialog .form-group.special img {
    margin-left: 20px;
    height: 30px;
    margin-top: 5px;
}
.fund-dialog .form-group.special p.img-loading {
    margin-left: 20px;
    padding: 0;
    height: 40px;
    line-height: 40px;
    text-align: center;
}
.fund-dialog .btn-group {
  margin-top: 20px;
  overflow: hidden;
}
.fund-dialog .btn-group .el-button {
    float: right;
    width: 100px;
}
.fund-dialog .btn-group .submit {
    margin-left: 30px;
}



