
.seller-announce .title {
  color: #666666;
  border-bottom: 1px solid #ddd;
  height: 50px;
  line-height: 50px;
  padding: 0 20px;
  font-weight: bold;
}
.seller-announce .announce {
  width: 380px;
  margin-top: 20px;
  background: #fff;
}
.seller-announce .announce p {
    padding: 20px;
    font-size: 12px;
    color: #666;
}
.seller-announce .delivery {
  width: 380px;
  margin-top: 20px;
  background: #fff;
}
.seller-announce .delivery ul {
    padding: 20px;
    font-size: 12px;
    color: #666;
}
.seller-announce .delivery ul li {
      height: 22px;
      line-height: 22px;
}
.seller-announce .points {
  width: 380px;
  margin-top: 20px;
  background: #fff;
}
.seller-announce .points .points-content {
    padding: 10px 20px 20px;
    border-bottom: 1px solid #ddd;
}
.seller-announce .points .points-content .top {
      overflow: hidden;
}
.seller-announce .points .points-content .top .score {
        float: left;
        font-size: 48px;
        color: #FF644C;
        font-weight: bold;
}
.seller-announce .points .points-content .top .count {
        float: left;
        margin-left: 20px;
        margin-top: 15px;
}
.seller-announce .points .points-content .top .count span {
          color: #999999;
}
.seller-announce .points .points-content .title2 {
      color: #666666;
      height: 50px;
      line-height: 50px;
      font-weight: bold;
      display: inline-block;
}
.seller-announce .points .points-content .orderCounnt {
      display: inline-block;
      margin-left: 20px;
      color: #FF644D;
      font-weight: bold;
}
.seller-announce .points .points-content ul li {
      overflow: hidden;
      margin-top: 10px;
}
.seller-announce .points .points-content ul .point {
      float: left;
      width: 15%;
}
.seller-announce .points .points-content ul .el-progress {
      float: left;
      width: 85%;
      /*.el-progress-bar__outer {*/
      /*height: 10px !important;*/
      /*}*/
}



