
.home {
  background: #F7F7F7;
}
.home .main {
    width: 100%;
    height: 82vh;
    background: url("/loong/static/img/bg@3x.png") center;
}
.home .main .download_base {
      position: absolute;
      bottom: 0px;
      z-index: 100;
      left: calc(50% - 150px);
}
.home .main .download_base a {
        border-right: 3px solid #ddd;
        padding: 0 20px;
        cursor: pointer;
}
.home .main .download_base a:last-child {
        border-right: 0;
}
.home .main .container {
      width: 1250px;
      margin: 0 auto;
}
.home .main .panel {
      width: 1180px;
      padding-top: 50px;
      -webkit-transition-duration: 1s;
              transition-duration: 1s;
}
.home .main .panel.showMap {
        padding-top: 60px;
}
.home .main .panel.showMap .showMapHide,
        .home .main .panel.showMap .download_container {
          display: none;
}
.home .main .panel.showMap .search .select-city {
          margin-left: 0;
}
.home .main .panel.showMap .search__resalut {
          opacity: 1 !important;
}
.home .main .panel h3 {
        font-size: 36px;
        color: #332E2E;
        letter-spacing: 1.12px;
        text-align: center;
}
.home .main .panel h3 span {
          font-family: NotoSansCJK-Black;
          font-size: 35px;
          color: #FF8428;
          letter-spacing: 1.09px;
}
.home .main .panel h1 {
        font-size: 64px;
}
.home .main .panel h1 span {
          font-weight: 300;
}
.home .main .panel .download_container {
        z-index: 100;
        width: 60%;
        padding-top: 50px;
        overflow: hidden;
        margin: 0 auto;
}
.home .main .panel .download_container .download_vessel:last-child {
          margin-left: 30px;
}
.home .main .panel .download_container .download_vessel {
          text-align: left;
          float: left;
}
.home .main .panel .download_container .download_vessel img {
            width: 220px;
            height: 220px;
}
.home .main .panel .download_container .download_vessel p {
            font-family: MicrosoftYaHei;
            font-size: 16px;
            color: #332E2E;
            letter-spacing: 0;
}
.home .main .panel .download_container .download_vessel h1 {
            font-family: NotoSansCJK-Black;
            font-size: 26px;
            color: #FB4E34;
            letter-spacing: 0;
            margin-bottom: 10px;
}
.home .main .panel .download_container .download_vessel .download_btn_group {
            margin-top: 20px;
}
.home .main .panel .download_container .download_vessel .download_btn_group span {
              background: url("/loong/static/img/Group11@2x.png") no-repeat;
              display: inline-block;
              width: 290px;
              height: 39px;
              background-size: 100% 100%;
}
.home .main .panel .search {
        width: 900px;
        margin: 20px auto;
}
.home .main .panel .search .search__resalut {
          -webkit-transition-duration: 1s;
                  transition-duration: 1s;
          opacity: 0;
          position: absolute;
          width: 1050px;
          top: 100px;
          z-index: 1;
          -webkit-box-shadow: 2px 2px 10px -2px #a9a9a9;
                  box-shadow: 2px 2px 10px -2px #a9a9a9;
}
.home .main .panel .search .search__resalut .address__list__wrapper {
            /* height: 600px; */
            height: 50vh;
            float: left;
            width: 380px;
            background: #fff;
            overflow: hidden;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__num {
              font-size: 18px;
              color: #333;
              margin: 0;
              text-align: center;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__num span {
                margin-left: 10px;
                position: relative;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list {
              background: #fff;
              padding: 20px;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list ul {
                overflow-x: hidden;
                overflow-y: auto;
                /* height: 476px; */
                height: 50vh;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list ul li.active a {
                  background-color: #f2f5f7;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list ul li.active i {
                  color: #FF644C;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list ul li a {
                  position: relative;
                  display: block;
                  line-height: 1.6em;
                  padding: 15px 15px 15px 45px;
                  position: relative;
                  display: block;
                  line-height: 1.6em;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list ul li a:hover {
                    background-color: #f2f5f7;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list ul li a i {
                    position: absolute;
                    color: #666;
                    left: 15px;
                    top: 20px;
                    font-size: 20px !important;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list ul li a h3 {
                    font-size: 16px;
                    color: #333;
                    font-weight: 700;
                    margin-bottom: 10px;
}
.home .main .panel .search .search__resalut .address__list__wrapper .address__list ul li a p {
                    margin: 3px 0;
                    font-size: 14px;
                    color: #666666;
                    line-height: 18px;
}
.home .main .panel .search .search__resalut .google-map {
            overflow: hidden;
}
.home .main .panel .search .search__resalut .google-map #map {
              width: 100%;
              height: 50vh;
}
.home .main .panel .search .select-city {
          float: left;
          width: 142px;
          height: 60px;
          text-align: center;
          line-height: 60px;
          background: #fff;
          border: 1px solid #dcdfe6;
          border-right: none;
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;
          margin-left: 170px;
}
.home .main .panel .search .select-city .city__choose {
            display: inline-block;
            width: 104px;
            position: relative;
            text-align: center;
            color: #333333;
            padding: 0 20px;
}
.home .main .panel .search .select-city .city__choose span {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
              width: 82px;
              position: relative;
}
.home .main .panel .search .select-city .city__choose img {
              margin-bottom: -3px;
}
.home .main .panel .search .el-input {
          float: left;
          width: 800px;
          height: 60px;
}
.home .main .panel .search .el-input .el-input__inner {
            height: 60px;
            border-right: none;
            border-radius: 0;
}
.home .main .panel .search .search__btn {
          float: left;
          width: 100px;
          height: 60px;
          border-bottom-left-radius: 0;
          border-top-left-radius: 0;
}
.home .main .panel p {
        color: #999999;
        font-size: 14px;
}
.home .main .panel p:first-child {
          margin-top: 33px;
          margin-bottom: 10px;
}
.map__show__info h3 {
  font-size: 18px !important;
  color: #333 !important;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.map__show__info p {
  font-size: 13px !important;
  color: #999 !important;
  margin-top: 2px !important;
}
.map__show__info button {
  margin-top: 10px;
}
.city__choose__container .top {
  font-size: 12px;
  color: #333333;
  margin: 3px 10px 15px 10px;
}
.city__choose__container .top button {
    margin-left: 10px;
}
.city__choose__container .top .city__search {
    width: 130px;
}
.city__choose__container .city__list {
  margin-top: 20px;
}
.city__choose__container .city__list ul li {
    float: left;
    padding: 5px;
    margin: 0 10px;
    cursor: pointer;
}
.city__choose__container .city__list ul li:hover {
      color: #FF644C;
}
.located {
  clear: both;
  cursor: pointer;
  padding-top: 10px;
  margin-left: 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.located img {
    margin-bottom: -4px;
}



