  .code-img {
    height: 40px;
    top: -40px;
    left: 360px;
    img {
      width: 90px;
    } 
  }
}
.detail-dialog {
  .el-dialog__body {
    margin: 0 50px;
    padding: 0
  }
  .el-radio__input.is-checked + .el-radio__label {
    color: #606266;
  }
}
.el-dialog__headerbtn {
  z-index: 9999;
}
.wechat-pay {
  .el-dialog__footer {
    margin-top: -15px;
  }
}
.mes_btn:hover{
  color: #666666;
  border: 1px solid #999999;
  background-color: #dddddd;
}
.el-input--small .el-input__inner {
  width: 120px;
}
.deleteComment {
  text-align: center;
}  
