
@charset "UTF-8";
.delivery__detail__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
  background: #fff;
}
.delivery__detail__container .detail__wrapper {
    border: 1px solid #ddd;
}
.delivery__detail__container .detail__wrapper .detail__out__wrapper {
      display: none;
}
.delivery__detail__container .detail__wrapper .info {
      float: left;
      width: 450px;
      padding: 60px;
}
.delivery__detail__container .detail__wrapper .info .step__container {
        border-left: 1px solid #ddd;
        height: 150px;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__wrapper {
          top: 50%;
          -webkit-transform: translateY(-50%);
                  transform: translateY(-50%);
}
.delivery__detail__container .detail__wrapper .info .step__container.green .icon__wrapper {
          border: 1px solid #67C33A;
}
.delivery__detail__container .detail__wrapper .info .step__container.green .icon__wrapper .iconfont {
            color: #67C33A;
}
.delivery__detail__container .detail__wrapper .info .step__container.green .step__con .status span {
          color: #67C33A;
}
.delivery__detail__container .detail__wrapper .info .step__container.black .icon__wrapper {
          border: 1px solid #333;
}
.delivery__detail__container .detail__wrapper .info .step__container.black .icon__wrapper .iconfont {
            color: #333;
}
.delivery__detail__container .detail__wrapper .info .step__container.black .step__con .status span {
          color: #333;
}
.delivery__detail__container .detail__wrapper .info .step__container.gray .icon__wrapper {
          border: 1px solid #ddd;
}
.delivery__detail__container .detail__wrapper .info .step__container.gray .icon__wrapper .iconfont {
            color: #ddd;
}
.delivery__detail__container .detail__wrapper .info .step__container.gray .step__con {
          background: #f5f5f5;
}
.delivery__detail__container .detail__wrapper .info .step__container.gray .step__con .status span {
            color: #ddd;
}
.delivery__detail__container .detail__wrapper .info .step__container.gray .step__con .status p {
            color: #ddd;
}
.delivery__detail__container .detail__wrapper .info .step__container.gray .step__con .arrow span {
            border-color: #f5f5f5 transparent transparent;
}
.delivery__detail__container .detail__wrapper .info .step__container .icon__wrapper {
          position: absolute;
          left: -16px;
          background: #fff;
          border-radius: 100%;
          width: 32px;
          height: 32px;
          text-align: center;
          line-height: 32px;
          top: 29px;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con {
          border: 1px solid #ddd;
          border-radius: 4px;
          background: #fff;
          height: 90px;
          padding: 0 30px;
          margin-left: 40px;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con .arrow {
            position: absolute;
            bottom: 52px;
            left: 0;
            transform: rotate(90deg);
            -ms-transform: rotate(90deg);
            /* IE 9 */
            -moz-transform: rotate(90deg);
            /* Firefox */
            -webkit-transform: rotate(90deg);
            /* Safari 和 Chrome */
            -o-transform: rotate(90deg);
            /* Opera */
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con .arrow em {
              border-color: #ddd transparent transparent;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con .arrow span {
              border-color: #FFF transparent transparent;
              top: -1px;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con .arrow * {
            display: block;
            border-width: 8px;
            position: absolute;
            border-style: solid dashed dashed dashed;
            font-size: 0;
            line-height: 0;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con .status span {
            display: block;
            margin-bottom: 10px;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con .status p {
            position: relative;
            font-size: 12px;
            color: #666;
            margin-bottom: 10px;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con .status p .phone {
              border-left: 1px solid #CCCCCC;
              position: absolute;
              right: 0;
              top: 0;
              padding-left: 20px;
}
.delivery__detail__container .detail__wrapper .info .step__container .step__con .status p .phone i {
                cursor: pointer;
}
.delivery__detail__container .detail__wrapper .map {
      overflow: hidden;
}
.delivery__detail__container .detail__wrapper .map #map {
        width: 100%;
        height: 570px;
}
.map__show__info h3 {
  font-size: 18px;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.map__show__info p {
  margin: 0 0 5px;
  font-size: 14px;
  color: #666666;
  font-weight: 600;
}



