
.order__list__wrapper {
  padding: 20px 0;
  border-bottom: 1px solid #ddd;
}
.order__list__wrapper .el-col {
    height: 90px;
}
.order__list__wrapper:last-child {
    border: 0;
}
.order__list__wrapper img.orderImg {
    width: 130px;
    height: 90px;
    margin-right: 20px;
    float: left;
}
.order__list__wrapper .info {
    overflow: hidden;
    text-align: left;
}
.order__list__wrapper .info .name {
      font-size: 16px;
      margin-bottom: 10px;
}
.order__list__wrapper .info p {
      padding: 5px 0;
}
.order__list__wrapper .u-middle button {
    margin-left: 0 !important;
}
.order__list__wrapper .el-button + .el-button {
    margin-top: 5px;
}
.order__list__wrapper .order__status {
    margin-bottom: 5px;
    display: block;
}
.el-dialog {
  color: #333333;
}
.el-dialog .el-dialog__header {
    margin-left: 30px;
    font-weight: bold;
}
.el-dialog .tableTop {
    border-top: 1px solid #DDDDDD;
}
.el-dialog .tableTop,
  .el-dialog .tableLeft {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #DDDDDD;
}
.el-dialog .tableRadio {
    margin-top: 10px;
}
.el-dialog .tableRadio .star-red {
      color: #FF644C;
}
.el-dialog .tableRadio div {
      height: 30px;
      line-height: 30px;
}
.el-dialog .radios div {
    display: inline-block;
    margin-left: 1.8rem;
}
.el-dialog .other {
    margin-bottom: 2rem;
}
.el-dialog .money {
    color: #FF644C;
}



