
.content {
  border: 1px solid #EBEBEB;
  height: 160px;
  padding: 20px;
  position: relative;
}
.content.active:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: 2px solid;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -o-border-image: linear-gradient(to bottom, #FF644C 25%, white 25%, white 75%, #FF644C 75%) 1;
       border-image: -webkit-gradient(linear, left top, left bottom, color-stop(25%, #FF644C), color-stop(25%, white), color-stop(75%, white), color-stop(75%, #FF644C)) 1;
       border-image: linear-gradient(to bottom, #FF644C 25%, white 25%, white 75%, #FF644C 75%) 1;
    border-top: none;
    border-bottom: none;
}
.content.active:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border: 2px solid;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -o-border-image: linear-gradient(to right, #FF644C 25%, white 25%, white 75%, #FF644C 75%) 1;
       border-image: -webkit-gradient(linear, left top, right top, color-stop(25%, #FF644C), color-stop(25%, white), color-stop(75%, white), color-stop(75%, #FF644C)) 1;
       border-image: linear-gradient(to right, #FF644C 25%, white 25%, white 75%, #FF644C 75%) 1;
    border-left: none;
    border-right: none;
}
.address-card .name {
  color: #666666;
  padding-bottom: 12px;
  border-bottom: 1px solid #EBEBEB;
  font-weight: bold;
}
.address-card .phone {
  margin-top: 12px;
}
.address-card .phone span {
    font-size: 12px;
    color: #666666;
    margin-right: 10px;
}
.address-card .address {
  margin-top: 12px;
  font-size: 12px;
  color: #666666;
  line-height: 22px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.address-card .out-distance {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 160px;
  background: rgba(255, 255, 255, 0.7);
  display: table;
}
.address-card .out-distance .out-content {
    margin: 0 auto;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    padding: 22px 0;
}
.address-card .out-distance .out-content img {
      width: 53%;
}
.edit__address {
  position: absolute;
  right: 0;
  z-index: 1;
  color: #ccc;
}



