
.evaluate__detail__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
  background: #fff;
}
.evaluate__detail__container .evaluate__detail__wrapper {
    padding: 40px 100px;
    border: 1px solid #ddd;
}
.evaluate__detail__container .evaluate__detail__wrapper .detail__wrapper {
      display: none;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info {
      padding-bottom: 40px;
      margin-bottom: 40px;
      border-bottom: 1px solid #ddd;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .info__detail {
        height: 90px;
        overflow: hidden;
        padding-right: 20px;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .info__detail .u-float-l {
          width: 60%;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .info__detail .u-float-l i {
            position: relative;
            top: -1px;
            float: left;
            margin-right: 10px;
            font-size: 20px;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .info__detail .u-float-l span {
            text-overflow: ellipsis;
            white-space: nowrap;
            cursor: pointer;
            overflow: hidden;
            color: #333;
            width: 90%;
            float: left;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .info__detail .u-float-r {
          width: 40%;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .info__detail .evaluate__time p {
          text-align: right;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .info__detail .evaluate__time span {
          color: #333;
          margin-left: 10px;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .el-col {
        height: 90px;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info img {
        width: 130px;
        height: 90px;
        float: left;
        margin-right: 30px;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info h2 {
        font-size: 18px;
        margin-bottom: 25px;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .location i {
        font-size: 20px;
        float: left;
        margin-right: 10px;
}
.evaluate__detail__container .evaluate__detail__wrapper .shop__info .location span {
        overflow: hidden;
        color: #333;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content {
      padding-left: 20px;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content .el-rate {
        top: -3px;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content textarea {
        border: 1px solid #dcdfe6;
        height: 150px;
        background: #fafafa;
        padding: 20px;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content .img__container .img__btn {
        position: absolute;
        width: 33px;
        height: 60px;
        background: #F5F5F5;
        border-radius: 4px;
        cursor: pointer;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        text-align: center;
        line-height: 60px;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content .img__container .img__btn.btn__next {
          right: 0;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content .img__container .img__btn i {
          font-size: 28px;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content .img__container .img__btn.swiper-button-disabled i {
          color: #ccc;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content .img__container .img__wrapper {
        width: 590px;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content .img__container .img__wrapper .swiper-slide {
          height: 90px;
}
.evaluate__detail__container .evaluate__detail__wrapper .evaluate__list .el-form-item__content .img__container .img__wrapper .swiper-slide img {
            width: 100%;
            height: 100%;
            cursor: -webkit-zoom-in;
            cursor: zoom-in;
}



