
.my__collection__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
}
.my__collection__container .collection__wrapper .collection__list {
    padding: 10px;
}
.my__collection__container .collection__wrapper .collection__list .el-col {
      padding: 10px;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper {
      border: 1px solid #ddd;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper:hover .top__del {
        display: block;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper img.shopImg {
        width: 100%;
        height: 150px;
        cursor: pointer;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .shop__name {
        margin: 10px 0 10px 0;
        color: #333;
        text-align: center;
        font-weight: 600;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .shop__rate {
        text-align: center;
        margin-bottom: 10px;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .shop__rate .raty__num {
          color: #888;
          font-size: 12px;
          position: relative;
          top: 2px;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .num__list {
        text-align: center;
        font-size: 12px;
        color: #666;
        margin-bottom: 20px;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .delete__btn__wrapper {
        text-align: center;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        overflow: hidden;
        background: rgba(51, 51, 51, 0.7);
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .delete__btn__wrapper p {
          margin-bottom: 15px;
          color: #fff;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .top__del {
        display: none;
        position: absolute;
        top: 0px;
        right: 0px;
        background: #FF644C;
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        cursor: pointer;
        background: rgba(51, 51, 51, 0.5);
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .top__del:hover {
          background: #FF644C;
}
.my__collection__container .collection__wrapper .collection__list .collection__list__wrapper .top__del i {
          color: #fff;
          font-size: 14px;
}



