
.pay-method {
  width: 700px;
  float: left;
}
.pay-method h2 {
    font-size: 18px;
    color: #333333;
    padding-bottom: 10px;
}
.pay-method h2 span {
      color: #FF644D !important;
}
.pay-method .surplus {
    color: #858585;
    height: 17px;
    line-height: 17px;
}
.pay-method .surplus img {
      margin-right: 5px;
      margin-bottom: -2px;
}
.pay-method .el-alert {
    margin-top: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
}
.pay-method .el-alert i.el-icon-close {
      display: none;
}
.pay-method .balance {
    border: 1px solid #EBEBEB;
    padding: 0 15px;
    margin-top: 20px;
    border-radius: 4px;
    position: relative;
}
.pay-method .balance.active {
      background: #fff;
}
.pay-method .balance .el-radio .el-radio__label {
      font-size: 16px;
      color: #666;
}
.pay-method .balance p {
      width: 336px;
      line-height: 22px;
}
.pay-method .balance p:first-child {
        margin-top: 15px;
        color: #333333;
        font-size: 15px;
}
.pay-method .balance .exchange-rate {
      margin-bottom: 15px;
      color: #999999;
      font-size: 12px;
}
.pay-method .balance img.icon {
      position: absolute;
      top: 30px;
      right: 20px;
      -webkit-transform: translateY(-30%);
              transform: translateY(-30%);
}
.pay-method .credit-card {
    border: 1px solid #EBEBEB;
    padding: 0 15px;
    margin-top: 20px;
    border-radius: 4px;
    position: relative;
}
.pay-method .credit-card.active {
      background: #fff;
}
.pay-method .credit-card .el-radio .el-radio__label {
      font-size: 16px;
      color: #666;
}
.pay-method .credit-card p {
      width: 336px;
      line-height: 22px;
}
.pay-method .credit-card p:first-child {
        margin-top: 15px;
        color: #333333;
        font-size: 15px;
}
.pay-method .credit-card .exchange-rate {
      margin-bottom: 15px;
      color: #999999;
      font-size: 12px;
}
.pay-method .credit-card .icon-list {
      position: absolute;
      top: 30px;
      right: 20px;
      -webkit-transform: translateY(-30%);
              transform: translateY(-30%);
      overflow: hidden;
}
.pay-method .credit-card .icon-list img {
        float: left;
}
.pay-method .credit-card .icon-list img:first-child {
          margin-right: 10px;
}
.pay-method .credit-card ul.card-list {
      padding-top: 20px;
      margin-bottom: 20px;
      border-top: 1px solid #EBEBEB;
      overflow: hidden;
}
.pay-method .credit-card ul.card-list li {
        float: left;
        width: 160px;
        height: 60px;
        border: 1px solid #EBEBEB;
        position: relative;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        cursor: pointer;
        margin-left: 29px;
}
.pay-method .credit-card ul.card-list li:first-child {
          margin-left: 0;
}
.pay-method .credit-card ul.card-list li span:first-child {
          width: 75%;
          position: absolute;
          top: 50%;
          left: 50%;
          -webkit-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%);
          margin-top: -5px;
}
.pay-method .credit-card ul.card-list li span img {
          width: 1.2rem;
          margin-bottom: -4px;
}
.pay-method .credit-card ul.card-list li span span {
          font-size: 16px;
          margin-left: 6px;
}
.pay-method .credit-card ul.card-list li span.renminbi {
          color: #999999;
          font-size: 14px;
          font-weight: normal;
          position: absolute;
          top: 50%;
          left: 50%;
          -webkit-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%);
          margin-top: 12px;
}
.pay-method .credit-card ul.card-list .active {
        border-color: #ff644d;
        background: url("../../assets/icon/choose@2x.png") no-repeat right bottom;
        background-size: 1.5rem;
}



