
.address__list__wrapper {
  border: 1px solid #ddd;
  padding: 15px 20px;
  border-radius: 4px;
  min-height: 132px;
}
.address__list__wrapper .list__header {
    padding-bottom: 15px;
    border-bottom: 1px dashed #ddd;
    margin-bottom: 15px;
}
.address__list__wrapper .list__header h2 {
      font-size: 16px;
      display: inline-block;
      font-weight: normal;
      color: #333;
}
.address__list__wrapper .list__header .list__todo {
      float: right;
}
.address__list__wrapper .list__header .list__todo i {
        font-size: 17px;
        margin-left: 10px;
        cursor: pointer;
        color: #666;
}
.address__list__wrapper .list__header .list__todo i.icon-zhanghushezhi-bianjiicon1 {
          font-size: 16px;
}
.address__list__wrapper .address__detail,
  .address__list__wrapper .tel {
    color: #888;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.address__list__wrapper .address__detail {
    margin-bottom: 10px;
}
.address__list__wrapper .delete__btn__wrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    background: rgba(51, 51, 51, 0.7);
    text-align: center;
}
.address__list__wrapper .delete__btn__wrapper p {
      margin-bottom: 15px;
      color: #fff;
}
.address__list__wrapper .add__address {
    text-align: center;
}
.address__list__wrapper .add__address i {
      font-size: 20px;
}
.address__list__wrapper .add__address span {
      margin-left: 20px;
}



