
.evaluate__order__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
  background: #fff;
}
.evaluate__order__container .evaluate__order__wrapper .order__wrapper {
    display: none;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info .info__detail {
    overflow: hidden;
    padding-right: 20px;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info .el-col {
    height: 90px;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info img {
    width: 130px;
    height: 90px;
    float: left;
    margin-right: 30px;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info h2 {
    font-size: 18px;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info .location {
    margin-top: 25px;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info .location i {
      position: relative;
      top: -1px;
      font-size: 20px;
      float: left;
      margin-right: 10px;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info .location span {
      text-overflow: ellipsis;
      white-space: nowrap;
      cursor: pointer;
      overflow: hidden;
      color: #333;
      width: 90%;
      float: left;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info .eval__star__wrapper:first-child {
    margin-bottom: 30px;
}
.evaluate__order__container .evaluate__order__wrapper .shop__info .eval__star__wrapper .el-rate {
    margin-left: 20px;
    top: -2px;
}
.evaluate__order__container .evaluate__order__wrapper .evaluate__textarea {
    margin: 40px 0;
}
.evaluate__order__container .evaluate__order__wrapper .evaluate__textarea textarea {
      height: 150px;
      background: #fafafa;
      padding: 20px;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container {
    float: left;
    width: 340px;
    margin-right: 40px;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper {
      padding: 10px;
      margin-top: 20px;
      height: 340px;
      border: 1px solid #ddd;
      overflow: hidden;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper ul li {
        border: 1px solid #ddd;
        position: relative;
        width: 84px;
        height: 84px;
        float: left;
        margin: 10px;
        border-radius: 4px;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper ul li img {
          width: 100%;
          height: 100%;
          display: none;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper ul li .close__btn {
          display: none;
          position: absolute;
          top: -8px;
          right: -10px;
          cursor: pointer;
          background: #FF644C;
          border-radius: 100%;
          width: 20px;
          height: 20px;
          text-align: center;
          line-height: 20px;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper ul li .close__btn i {
            font-size: 14px;
            color: #fff;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper ul li .loading {
          text-align: center;
          top: 50%;
          -webkit-transform: translateY(-50%);
                  transform: translateY(-50%);
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper ul li .loading i {
            font-size: 20px;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper ul li.add__img {
          border: 1px dashed #ddd;
          border-radius: 6px;
          background: #fafafa;
          line-height: 86px;
          text-align: center;
          cursor: pointer;
}
.evaluate__order__container .evaluate__order__wrapper .upload__img__container .upload__img__wrapper ul li.add__img i {
            font-size: 25px;
            color: #ccc;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food {
    overflow: hidden;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper {
      margin-top: 20px;
      padding: 20px;
      border: 1px solid #ddd;
      min-height: 340px;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li {
        float: left;
        width: 50%;
        padding: 10px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li img {
          width: 40px;
          height: 40px;
          margin-right: 10px;
          float: left;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li span {
          color: #333;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li .c-dis-table {
          height: 40px;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li .c-dis-table.zan {
            position: absolute;
            right: 0;
            cursor: pointer;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li .c-dis-table.zan i {
              color: #ccc;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li .c-dis-table.zan .icon-icon_recommend_xuanzhong {
              display: none;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li .c-dis-table.zan .icon-icon_recommend {
              display: block;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li .c-dis-table.zan.active i {
              color: #FF644C;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li .c-dis-table.zan.active .icon-icon_recommend_xuanzhong {
              display: block;
}
.evaluate__order__container .evaluate__order__wrapper .recommend__food .recommend__food__wrapper ul li .c-dis-table.zan.active .icon-icon_recommend {
              display: none;
}
.evaluate__order__container .evaluate__order__wrapper .submit__wrapper {
    text-align: center;
    margin-top: 40px;
}



