
@charset "UTF-8";
.order__detail__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
  background: #fff;
}
.order__detail__container .order__detail__wrapper button {
    margin-left: 0 !important;
    display: block;
}
.order__detail__container .order__detail__wrapper .el-button + .el-button {
    margin-top: 5px;
}
.order__detail__container .order__detail__wrapper .info__list {
    border: 1px solid #ddd;
    margin-right: 40px;
    min-height: 990px;
}
.order__detail__container .order__detail__wrapper .info__list .list__item {
      margin-bottom: 60px;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .el-form-item {
        margin-bottom: 0;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .title {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid #ddd;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .title h2 {
          font-size: 16px;
}
.order__detail__container .order__detail__wrapper .info__list .list__item img {
        width: 50px;
        height: 50px;
        float: left;
        margin-right: 20px;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .shop__name {
        font-size: 16px;
        color: #333;
        height: 50px;
        overflow: hidden;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .shop__tel {
        color: #999;
        height: 50px;
        padding-left: 20px;
        word-break: break-all;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper {
        margin-top: 40px;
        text-align: center;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .goods__list__header .el-col:first-child,
        .order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .goods__list__con .el-col:first-child {
          text-align: left;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .goods__list__con {
          margin-top: 20px;
          color: #333;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .goods__list__con .el-col {
            margin-bottom: 20px;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .pay__detail {
          margin-top: 27px;
          text-align: right;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .pay__detail p {
            margin-bottom: 10px;
            margin-right: 10px;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .pay__detail h3 {
            font-size: 16px;
            display: inline-block;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .pay__detail .money {
            color: #FF644C;
            font-size: 24px;
            margin-left: 10px;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .goods__list__wrapper .pay__detail .oldmoney {
            text-decoration: line-through;
            margin-left: 5px;
}
.order__detail__container .order__detail__wrapper .info__list .list__item .todo--btn {
        position: absolute;
        top: 5px;
        right: 0;
}
.order__detail__container .order__detail__wrapper .info__step {
    margin-left: 40px;
}
.order__detail__container .order__detail__wrapper .info__step .step__container {
      border-left: 1px solid #ddd;
      height: 339px;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__wrapper {
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
}
.order__detail__container .order__detail__wrapper .info__step .step__container.green .icon__wrapper {
        border: 1px solid #67C33A;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.green .icon__wrapper .iconfont {
          color: #67C33A;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.green .step__con .status span {
        color: #67C33A;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.black .icon__wrapper {
        border: 1px solid #333;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.black .icon__wrapper .iconfont {
          color: #333;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.black .step__con .status span {
        color: #333;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.gray .icon__wrapper {
        border: 1px solid #ddd;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.gray .icon__wrapper .iconfont {
          color: #ddd;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.gray .step__con {
        background: #f5f5f5;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.gray .step__con .status span {
          color: #ddd;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.gray .step__con .status p {
          color: #ddd;
}
.order__detail__container .order__detail__wrapper .info__step .step__container.gray .step__con .arrow span {
          border-color: #f5f5f5 transparent transparent;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .icon__wrapper {
        position: absolute;
        left: -16px;
        background: #fff;
        border-radius: 100%;
        width: 32px;
        height: 32px;
        text-align: center;
        line-height: 32px;
        top: 29px;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con {
        border: 1px solid #ddd;
        border-radius: 4px;
        background: #fff;
        height: 90px;
        padding: 0 30px;
        margin-left: 40px;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con .arrow {
          position: absolute;
          bottom: 52px;
          left: 0;
          transform: rotate(90deg);
          -ms-transform: rotate(90deg);
          /* IE 9 */
          -moz-transform: rotate(90deg);
          /* Firefox */
          -webkit-transform: rotate(90deg);
          /* Safari 和 Chrome */
          -o-transform: rotate(90deg);
          /* Opera */
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con .arrow em {
            border-color: #ddd transparent transparent;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con .arrow span {
            border-color: #FFF transparent transparent;
            top: -1px;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con .arrow * {
          display: block;
          border-width: 8px;
          position: absolute;
          border-style: solid dashed dashed dashed;
          font-size: 0;
          line-height: 0;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con .status span {
          display: block;
          margin-bottom: 10px;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con .status p {
          position: relative;
          font-size: 12px;
          color: #666;
          margin-bottom: 10px;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con .time {
          float: right;
}
.order__detail__container .order__detail__wrapper .info__step .step__container .step__con .time span {
            font-size: 12px;
}
.el-dialog {
  color: #333333;
}
.el-dialog .el-dialog__header {
    margin-left: 30px;
    font-weight: bold;
}
.el-dialog .tableTop {
    border-top: 1px solid #DDDDDD;
}
.el-dialog .tableTop,
  .el-dialog .tableLeft {
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #DDDDDD;
}
.el-dialog .other {
    margin-bottom: 2rem;
}
.el-dialog .tableRadio {
    margin-top: 10px;
}
.el-dialog .tableRadio .star-red {
      color: #FF644C;
}
.el-dialog .tableRadio div {
      line-height: 30px;
}
.el-dialog .money {
    color: #FF644C;
}



