
.balance__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
}
.balance__container .balance__wrapper {
    min-height: 0;
    background: none;
}
.balance__container .balance__wrapper .top__header {
      background: #fff;
      margin-bottom: 20px;
}
.balance__container .balance__wrapper .top__header h2 {
        margin-bottom: 15px;
}
.balance__container .balance__wrapper .top__header .balance__detail .money {
        font-size: 38px;
        color: #333;
        margin-right: 50px;
}
.balance__container .balance__wrapper .top__header .balance__detail button {
        position: relative;
        bottom: 9px;
}
.balance__container .balance__wrapper .transaction__wrapper {
      min-height: 642px;
}
.balance__container .balance__wrapper .transaction__wrapper .tran__header {
        padding: 20px;
}
.balance__container .balance__wrapper .transaction__wrapper .tran__header h2 {
          font-size: 16px;
          display: inline-block;
}
.balance__container .balance__wrapper .transaction__wrapper .tran__list {
        padding: 0 20px 20px 20px;
}
.balance__container .balance__wrapper .transaction__wrapper .tran__list th,
        .balance__container .balance__wrapper .transaction__wrapper .tran__list td {
          text-align: center;
}
.balance__container .balance__wrapper .transaction__wrapper .tran__list thead th {
          background: #fafafa;
          color: rgba(102, 102, 102, 0.5);
}
.balance__container .balance__wrapper .transaction__wrapper .tran__list thead .cell {
          font-weight: 500;
}
.tran__detail__wrapper {
  width: 400px;
  margin: 0 auto;
}



