
.my__coupon__container {
  width: 1250px;
  margin: 0 auto;
  margin-top: 20px;
}
.my__coupon__container .coupon__wrapper .coupon__list {
    padding: 10px;
}
.my__coupon__container .coupon__wrapper .coupon__list .el-col {
      padding: 10px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon {
      border: 1px solid #F0F0F0;
      border-radius: 4px;
      position: relative;
      overflow: hidden;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.history .shad--cover,
      .my__coupon__container .coupon__wrapper .coupon__list .card-coupon.history .history__tips {
        display: block;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.plat .left .title {
        color: #FF644C;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.plat .left .time {
        color: rgba(255, 100, 77, 0.5);
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.plat .right {
        background-color: #FF644C;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.plat .right .use__type .line {
          width: 85%;
          height: 1px;
          background: #fff;
          margin: 0 auto;
          top: 8px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.plat .right .use__type p {
          width: 48%;
          word-break: break-all;
          margin: 0 auto;
          position: relative;
          padding: 0 2px;
          color: rgba(255, 255, 255, 0.9);
          background: #FF644C;
          line-height: 13px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.bussiness .left .title {
        color: #FFB900;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.bussiness .left .time {
        color: rgba(255, 185, 0, 0.5);
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.bussiness .right {
        background-color: #FFB900;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon.bussiness .right .use__type p {
          color: rgba(255, 255, 255, 0.9);
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .left {
        padding: 15px 20px;
        height: 135px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        width: 75%;
        float: left;
        position: relative;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .left .title {
          font-size: 20px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .left .time {
          margin-top: 2px;
          font-size: 12px;
          margin-bottom: 10px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .left .des {
          font-size: 12px;
          padding: 2px 0;
          color: #999;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .left .point {
          width: 8px;
          height: 120px;
          position: absolute;
          top: 12px;
          right: -4px;
          z-index: 1;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .right {
        overflow: hidden;
        position: relative;
        height: 135px;
        text-align: center;
        color: #fff;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .right .price {
          font-size: 40px;
          margin: 27px 0 10px 0;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .right .price span {
            font-size: 14px;
            margin-left: 3px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .circle {
        width: 13px;
        height: 13px;
        background: #F0F0F0;
        border-radius: 100%;
        position: absolute;
        z-index: 1;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .circle.c--left {
          left: -7px;
          top: 62px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .circle.c--right {
          right: -7px;
          top: 62px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .circle.c--top {
          right: -6px;
          top: -7px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .circle.c--bottom {
          right: -6px;
          bottom: -7px;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .shad--cover {
        position: absolute;
        width: 100%;
        height: 135px;
        top: 0;
        z-index: 2;
        background: #ffffffa3;
        display: none;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .shad--cover img {
          width: 80px;
          position: absolute;
          top: 32px;
          left: 50%;
}
.my__coupon__container .coupon__wrapper .coupon__list .card-coupon .history__tips {
        padding: 10px 20px;
        background-color: #fafafa;
        position: relative;
        z-index: 1;
        display: none;
        color: #808080;
}



